loading
},
data() {
return {
show_reward: false,
inviteCode: "",
isTabOne: true,
menuTop: "",
page: 0,
desc_invite: "",
amountInvite: "",
total_amount: "",
isTabOne: true,
isTabTwo: false,
vant_loading1: false,
vant_finished1: false,
vant_loading2: false,
vant_finished2: false,
fans_list_page: 1,
yaoqing_list_page: 1,
fans_list: [{
member_avatar: "http://img.xiepinhui.com.cn/user/3141/avatar/15380184551.jpg?x-oss-process=image/resize,m_mfit,h_200,w_200",
member_name: "333",
order_amount: "1111",
is_smallshop: 1,
amount: 333,
is_smallshop: 0
},
{
member_avatar: "http://img.xiepinhui.com.cn/user/3141/avatar/15380184551.jpg?x-oss-process=image/resize,m_mfit,h_200,w_200",
member_name: "333",
order_amount: "1111",
is_smallshop: 1,
amount: 333,
is_smallshop: 0
}
],
yaoqing_list: [{
member_avatar: "http://img.xiepinhui.com.cn/user/3141/avatar/15380184551.jpg?x-oss-process=image/resize,m_mfit,h_200,w_200",
member_id: "97",
member_invite_num: "17",
member_mobile: "15217336505",
member_name: "本宝宝",
rank: 1
},
{
member_avatar: "http://img.xiepinhui.com.cn/user/3141/avatar/15380184551.jpg?x-oss-process=image/resize,m_mfit,h_200,w_200",
member_id: "97",
member_invite_num: "17",
member_mobile: "15217336505",
member_name: "本宝宝",
rank: 2
}
],
show_bottom: false,
showload: false
};
},
created() {
this.getfanred();
this.getFans_list();
},
mounted() {},
methods: {
async getfanred() {
let data = {
plat: 3,
account: this.account,
token: this.token
};
const [err, res] = await api.fanred(data);
if (res) {
this.inviteCode = res.data.member_code;
this.desc_invite = res.data.desc;
this.amountInvite = res.data.twomoney;
this.available_amount = res.data.red_amout; 
this.total_amount = res.data.total_amout; 

}
},
bindFans(codeInvite) {
let data = {
plat: 3,
account: this.account,
token: this.token,
code: codeInvite
};
const [err, res] = api.inviteweidian(data);
if (res) {
let codeStatus = res.data.code;
console.log("bindFans:", res);
if (codeStatus == 5001) {

this.$router.push("/center");
} else if (codeStatus == 5002) {

} else if (codeStatus == 5000) {

this.$router.push("/centerFull/partner/code");
} else if (codeStatus == 5003) {

} else if (codeStatus == 5004) {






} else if (codeStatus == 4029) {

this.$router.push({
path: "/centerFull/partner/applyStatic",
query: {
status: 0
}
});
} else if (codeStatus == 4020) {

this.$router.push("/center");
}
}
},

async getFans_list() {
this.showload = true;
let data = {
plat: 3,
account: this.account,
token: this.token,
page: this.fans_list_page
};
const [err, res] = await api.storefan1(data);
this.showload = false;
if (err) {
console.log("err", err);
return;
}
this.vant_loading1 = false;
this.fans_list = res.data.list;
this.fans_list_page++;
if (res.data && res.data.list.length < 1) {
this.vant_finished1 = true;
return;
}
},
async getYaoqingList() {
this.showload = true;
let data = {
plat: 3,
account: this.account,
token: this.token,
page: this.yaoqing_list_page
};
const [err, res] = await api.inviterankingweidian(data);
this.showload = false;
if (err) {
console.log("err", err);
return;
}
this.vant_loading2 = false;
this.yaoqing_list = res.data.list;
this.yaoqing_list_page++;
if (res.data && res.data.list.length < 1) {
this.vant_finished2 = true;
return;
}
},
myRewardFun() {},
goRecordFun() {},
myRewardFun() {},
goRecordFun() {},
copyFunction() {},
inviteFriendFun() {},
choseTab1() {
this.isTabOne = true;
this.isTabTwo = false;
},
choseTab2() {
this.isTabOne = false;
this.isTabTwo = true;
},
close_rewardFun() {}
},
computed: {
...mapGetters(["user", "account", "token"])
}
};




