
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import { isWeiXin } from "@/utils/comm.js";
import { api } from "@/utils/api.js";
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "",
props: {},
components: {
XHeader
},
data() {
return {
codeKey: "",
userName: "",

name: "",
phone: "",
store: "",
img1: "",
img2: "",
img1_sn: "",
img2_sn: "",
payFun: 1,
tips: false,
count: "365.00",
showRule: false,
ruleContent: "",

client: "", //alioss
fileSize: 500000,
iswx: isWeiXin()
};
},
created() {
this.codeKey = this.$route.query.codeKey || "";
this.userName = this.$route.query.userName || "";
if (isWeiXin()) {
this.payFun = 1;
} else {
this.payFun = 2;
}
this.getGuize();
},
mounted() {},
methods: {

async pay() {
this._check(); 
let data = {
plat: 4,
account: this.account,
token: this.token,
shop_name: this.store,
real_name: this.name,
mobile: this.phone,
sn_up: this.img1_sn,
sn_down: this.img2_sn,
openid: this.openid,
pay_code: 1, // 1微信 2支付宝 3银联
type: 3,
member_code: this.codeKey
};
const [err, res] = await api.addsmallshop(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
console.log("pay", res);

},

_check() {
if (!this.name) {
this.$vux.toast.text("请填写姓名");
return;
}
let reg = /^1[0-9]{10}$/;
if (!reg.test(this.phone)) {
this.$vux.toast.text("请输入正确的手机号");
return;
}
if (!this.store) {
this.$vux.toast.text("请填写正确店铺名称");
return;
}
if (!this.img1 || !this.img2) {
this.$vux.toast.text("请上传证件");
return;
}
if (!this.tips) {
this.$vux.toast.text("请勾选协议");
return;
}
this.$router.push("applyStatic");
},

upimg(e, name) {
let that = this;
let file = e.target.files[0];
let data = {
plat: 3,
token: this.token,
account: this.account,
type: 17,
num: 1
};
if (name === "img1") {
this._ossImg(data, file, name);
this.img1 = window.URL.createObjectURL(file);
} else {
this._ossImg(data, file, name);
this.img2 = window.URL.createObjectURL(file);
}
},

async _ossImg(data, file, name) {
const [err, res] = await api.getpaths(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}
if (res.code == "2000") {
this.client = new OSS.Wrapper({
secure: true,
accessKeyId: res.data.AccessKeyId,
accessKeySecret: res.data.AccessKeySecret,
bucket: res.data.bucket
});
this.client
.multipartUpload(res.data.files, file)
.then(function(result) {
if (name === "img1") {
this.img1_sn = res.data.sn;
} else {
this.img2_sn = res.data.sn;
}
console.log("result", result);
})
.catch(function(err) {
console.log("err", err);
});
}
},
async getGuize() {
let data = {
plat: 3,
account: this.account,
token: this.token,
type: 1
};
const [err, res] = await api.guize(data);
if (err) {
this.ruleContent = "看到我啦";
return;
}
this.ruleContent = res.data.list;
},

selectPayFun(type) {
switch (type) {
case 1:
this.payFun = 1;
break;
case 2:
this.payFun = 2;
break;
}
},

selectTips() {
this.tips = !this.tips;
},

ruleShow() {
this.showRule = true;
},
hiddenMask() {
this.showRule = false;
}






























},
computed: {
...mapGetters(["user", "account", "token"])
}
};



